
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'DynamicScreen',

  data() {
    return {
      component: null as any,
      isFetchReady: false,
    }
  },

  async created(){
    await this.$store.dispatch('CommonCodeModule/requestCommonCode')
    this.isFetchReady = true
  },

  methods: {
    updateComponent(param) {
      // dynamic import based on route param
      
      import(`@/views/${param.slice(0, 3).toLocaleLowerCase()}/${param.toLocaleLowerCase()}/${param.toLocaleLowerCase()}.vue`)
      .then(module => {
        setTimeout(()=>{
          this.component = module.default;
        }, 100)
        
      }).catch(error => {
        console.log(error);
        this.$router.replace('/error4')
      })
    }
  },
  
  beforeRouteEnter(to, from, next) {
    // When first entering the route call dynamic import component

    next((vm: any) => vm.updateComponent(to.params.dynamic));
  },
  
  beforeRouteUpdate(to, from, next) {
    // When changing from one dynamic route to another call dynamic import component
    
    this.updateComponent(to.params.dynamic);
    next();
  }
})

